"use server";

import { createClient } from "@supabase/supabase-js";

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
const supabaseServiceRoleKey = process.env.SUPABASE_SERVICE_ROLE_KEY;

if (!supabaseUrl) {
  throw new Error("NEXT_PUBLIC_SUPABASE_URL –Ω–µ –∑–∞–¥–∞–Ω");
}

if (!supabaseServiceRoleKey) {
  throw new Error("SUPABASE_SERVICE_ROLE_KEY –Ω–µ –∑–∞–¥–∞–Ω");
}

const supabaseAdmin = createClient(supabaseUrl, supabaseServiceRoleKey);

interface DeleteVoiceInput {
  uid: string;
  voiceId: string;
}

interface DeleteVoiceResult {
  success: boolean;
  error?: string;
}

export async function DeleteVoice({
  uid,
  voiceId,
}: DeleteVoiceInput): Promise<DeleteVoiceResult> {
  try {
    if (!uid) {
      return { success: false, error: "–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –Ω–∞–π–¥–µ–Ω" };
    }

    if (!voiceId) {
      return { success: false, error: "ID –≥–æ–ª–æ—Å–∞ –Ω–µ —É–∫–∞–∑–∞–Ω" };
    }

    // –°–Ω–∞—á–∞–ª–∞ –ø–æ–ª—É—á–∞–µ–º –∑–∞–ø–∏—Å—å –∏–∑ —Ç–∞–±–ª–∏—Ü—ã, —á—Ç–æ–±—ã –∏–∑–≤–ª–µ—á—å voice_id –∏ –Ω–∞–∑–≤–∞–Ω–∏–µ –≥–æ–ª–æ—Å–∞
    const { data: voiceRecord, error: fetchError } = await supabaseAdmin
      .from("voices")
      .select("voice_id, name")
      .eq("id", voiceId)
      .eq("uid", uid)
      .single();

    if (fetchError) {
      throw new Error(`–ù–µ —É–¥–∞–ª–æ—Å—å –Ω–∞–π—Ç–∏ –≥–æ–ª–æ—Å: ${fetchError.message}`);
    }

    if (!voiceRecord || !voiceRecord.voice_id) {
      throw new Error("–ù–µ –Ω–∞–π–¥–µ–Ω–æ –∑–Ω–∞—á–µ–Ω–∏–µ voice_id –¥–ª—è —ç—Ç–æ–≥–æ –≥–æ–ª–æ—Å–∞");
    }

    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –≤–µ–±—Ö—É–∫ —Å voice_id, –Ω–∞–∑–≤–∞–Ω–∏–µ–º –≥–æ–ª–æ—Å–∞ –∏ UUID –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    const webhookResponse = await fetch(
      "https://rueleven.ru/webhook/932aee6f-b554-45e9-b232-f7829b0a1d06",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ 
          voice_id: voiceRecord.voice_id,
          voice_name: voiceRecord.name || null,
          uuid: uid,
        }),
      }
    );

    if (!webhookResponse.ok) {
      const message = await webhookResponse.text();
      throw new Error(
        `–ù–µ —É–¥–∞–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –≤–µ–±—Ö—É–∫: ${
          message || webhookResponse.statusText
        }`
      );
    }

    // –£–¥–∞–ª—è–µ–º –∑–∞–ø–∏—Å—å –∏–∑ —Ç–∞–±–ª–∏—Ü—ã
    const { error } = await supabaseAdmin
      .from("voices")
      .delete()
      .eq("id", voiceId)
      .eq("uid", uid);

    if (error) {
      throw new Error(error.message);
    }

    return { success: true };
  } catch (error) {
    const message =
      error instanceof Error ? error.message : "–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–∞—è –æ—à–∏–±–∫–∞";
    console.error("[DeleteVoice]", message);
    return { success: false, error: message };
  }
}


