"use server";

import { createClient } from "@supabase/supabase-js";

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
const supabaseServiceRoleKey = process.env.SUPABASE_SERVICE_ROLE_KEY;

if (!supabaseUrl) {
  throw new Error("NEXT_PUBLIC_SUPABASE_URL –Ω–µ –∑–∞–¥–∞–Ω");
}

if (!supabaseServiceRoleKey) {
  throw new Error("SUPABASE_SERVICE_ROLE_KEY –Ω–µ –∑–∞–¥–∞–Ω");
}

const supabaseAdmin = createClient(supabaseUrl, supabaseServiceRoleKey);

interface UpdateVoiceInput {
  uid: string;
  voiceId: string;
  name: string;
  description?: string | null;
}

interface UpdateVoiceResult {
  success: boolean;
  error?: string;
}

export async function UpdateVoice({
  uid,
  voiceId,
  name,
  description,
}: UpdateVoiceInput): Promise<UpdateVoiceResult> {
  try {
    if (!uid) {
      return { success: false, error: "–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –Ω–∞–π–¥–µ–Ω" };
    }

    if (!voiceId) {
      return { success: false, error: "ID –≥–æ–ª–æ—Å–∞ –Ω–µ —É–∫–∞–∑–∞–Ω" };
    }

    if (!name || !name.trim()) {
      return { success: false, error: "–ò–º—è –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º" };
    }

    const trimmedName = name.trim();
    const trimmedDescription = description?.trim();

    const { error } = await supabaseAdmin
      .from("voices")
      .update({
        name: trimmedName,
        description: trimmedDescription || null,
      })
      .eq("id", voiceId)
      .eq("uid", uid);

    if (error) {
      throw new Error(error.message);
    }

    return { success: true };
  } catch (error) {
    const message =
      error instanceof Error ? error.message : "–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–∞—è –æ—à–∏–±–∫–∞";
    console.error("[UpdateVoice]", message);
    return { success: false, error: message };
  }
}


