import { supabase } from "./supabaseClient";
import type { PhotoAvatarRow } from "@/app/avatars/servers/UploadAvatar";

// –ö–µ—à –¥–ª—è prefetch –¥–∞–Ω–Ω—ã—Ö
let prefetchCache: {
  data: PhotoAvatarRow[] | null;
  timestamp: number;
  userId: string | null;
} = {
  data: null,
  timestamp: 0,
  userId: null,
};

const CACHE_DURATION = 30000; // 30 —Å–µ–∫—É–Ω–¥

export async function prefetchAvatarsData(): Promise<void> {
  try {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º –∫–µ—à
    const now = Date.now();
    if (
      prefetchCache.data &&
      prefetchCache.userId &&
      now - prefetchCache.timestamp < CACHE_DURATION
    ) {
      return; // –î–∞–Ω–Ω—ã–µ —É–∂–µ –≤ –∫–µ—à–µ –∏ —Å–≤–µ–∂–∏–µ
    }

    // –ü–æ–ª—É—á–∞–µ–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    const {
      data: { user },
    } = await supabase.auth.getUser();

    if (!user) {
      return;
    }

    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –Ω–µ –∑–∞–≥—Ä—É–∂–∞–µ–º –ª–∏ –º—ã —Ç–µ –∂–µ –¥–∞–Ω–Ω—ã–µ
    if (prefetchCache.userId === user.id && now - prefetchCache.timestamp < CACHE_DURATION) {
      return;
    }

    // –ó–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ –≤ —Ñ–æ–Ω–µ
    const { data, error } = await supabase
      .from("photo_avatars")
      .select("*")
      .eq("uid", user.id)
      .order("created_at", { ascending: false });

    if (!error && data) {
      prefetchCache = {
        data,
        timestamp: now,
        userId: user.id,
      };
    }
  } catch (error) {
    // –ò–≥–Ω–æ—Ä–∏—Ä—É–µ–º –æ—à–∏–±–∫–∏ prefetch - —ç—Ç–æ –Ω–µ –∫—Ä–∏—Ç–∏—á–Ω–æ
    console.debug("[prefetch] –û—à–∏–±–∫–∞ prefetch –∞–≤–∞—Ç–∞—Ä–æ–≤:", error);
  }
}

export function getPrefetchedAvatars(): PhotoAvatarRow[] | null {
  const now = Date.now();
  if (
    prefetchCache.data &&
    now - prefetchCache.timestamp < CACHE_DURATION
  ) {
    return prefetchCache.data;
  }
  return null;
}

export function clearPrefetchCache(): void {
  prefetchCache = {
    data: null,
    timestamp: 0,
    userId: null,
  };
}

